import React, { useState } from "react";
import GGSliderContent from "./GGSliderContent"
import GGSlide from "./GGSlide";
import { css } from "@emotion/react";
import GGArrow from "./GGArrow";
import ggCarouselContent from "../ggCarouselContent";

function GGCarousel() {
    const [ state, setState ] = useState({
        activeSlide: 0,
        translate: 0,
        transition: 0.45
    });

    const { activeSlide, translate, transition } = state;

    function nextSlide(){
        if ( activeSlide === (ggCarouselContent.length - 1)) {
            return setState({
                ...state,
                activeSlide: 0,
                translate: 0
            });
        } 
        
        if ( !(activeSlide === ggCarouselContent.length - 4) ) {
            setState({
                ...state,
                activeSlide: activeSlide + 1,
                translate: (35 * (activeSlide + 1)) 
            });
        }
    }

    return (
        <div className="carousel">
            <div className="slider">
                <GGSliderContent
                    translate={translate}
                    transition={transition}
                    numberSlides={ggCarouselContent.length}
                >
                {
                    ggCarouselContent.map((page) => {
                        return <GGSlide 
                            key={page.id}
                            title={page.title}
                            content={page.content}                        
                        />;
                    })
                }
                </GGSliderContent>
                <GGArrow
                    direction="right"
                    handleClick={nextSlide}
                />
            </div>
        </div>
    );
}

export default GGCarousel;