import React from "react";
import GGCarousel from "./GGCarousel";

function GoGreen(){
    return (
        <div id="go-green" className="go-green">
            <div className="gg-image-container">
                <img src="./imgs/pepper.png" alt="orange pepper"></img>
            </div>
            <div className="gg-content">
                <div className="gg-box-control">
                    <GGCarousel className="gg-carousel" />
                    <div className="logos">
                        <img className="logo-1" src="./imgs/logo1.png" alt="logo waitrose"></img>
                        <img className="logo-2" src="./imgs/logo2.png" alt="logo tesco"></img>
                        <img className="logo-3" src="./imgs/logo3.png" alt="logo ocado"></img>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default GoGreen;